<a-modal class="main" :title="props.isEdit?'编辑班级':'创建班级'" v-model:visible="props.visible" @cancel="onCancel"><a-form :label-col="labelCol" :wrapper-col="wrapperCol" :model="formModel" :rules="formRule"><a-form-item label="班级名称" name="name" v-bind="validateInfos.name"><a-input v-model:value="formModel.name" placeholder="填写班级名称" :maxlength="30"></a-input></a-form-item><a-form-item label="班级描述" name="remark" v-bind="validateInfos.remark"><a-text-area v-model:value="formModel.remark" placeholder="请输入班级描述" :maxlength="100" :showCount="false" style="height:50px;"></a-text-area></a-form-item><a-form-item label="班级教师" name="teacherId" v-bind="validateInfos.teacherId"><a-select v-model:value="formModel.teacherId" placeholder="请选择" :disabled="props.isEdit" :default-active-first-option="false" :filterOption="false"><a-select-option v-for="v in selectTeachers" :key="v.code" :value="v.code">{{v.name}}</a-select-option></a-select></a-form-item></a-form><template #footer><a-button key="back" type="primary" ghost @click="onCancel">取消</a-button><a-button key="submit" type="primary" @click="onSubmit">保存</a-button></template></a-modal>